<template>
  <div class="container">
    <div v-if="data">
      <div id="roadview"></div>
      <div class="apt-name">
        <span>{{ data.roadName }}  {{ data.buildYear }} </span>
        <h1>{{ data.apartmentName }}</h1>
      </div>
      <div class="flex-container">
        <div class="summarize-info icon-data-container">
          <icon-data-box icon="dollar" label=" " :value="getLastDeal" />
          <icon-data-box icon="up" label=" " value="" />
          <icon-data-box icon="handshake" label=" " :value="`${data.list.length}`" />
        </div>

        <div class="summarize-info">
          <div class="place-info-area">
            <h3>  </h3>
            <div>
              <div
                v-for="(item, idx) in getPlaceInfo"
                :key="`local-info-${item.label}`"
                class="place-info drag-block"
                :class="{ odd: (idx + 1) % 2 }"
              >
                <div>{{ item.label }}</div>
                <div>{{ item.value }}</div>
              </div>
            </div>
          </div>

          <div class="place-info-area small">
            <h3>  </h3>
            <div>
              <div v-for="item in getScoreInfo" class="score-info drag-block" :key="`score-info-${item.label}`">
                <div>{{ item.label }}</div>
                <div>{{ item.value }}/5</div>
              </div>
            </div>
          </div>
        </div>
        <apt-chart :data="countData" title="" label="m" header="  " />
        <apt-chart :data="amountData" title="" label="m" header="  " />
      </div>
    </div>
  </div>
</template>

<script>
import { getDealString } from "@/api/utils.js";
import { getAptDetail } from "@/api/apt";
import { mapState } from "vuex";
import IconDataBox from "../common/IconDataBox.vue";
import AptChart from "@/components/apt/common/AptChart";

/* global kakao */
export default {
  components: { IconDataBox, AptChart },
  name: "AptDetail",
  data() {
    return {
      roadview: null,
      roadviewClient: null,
      data: null,
      amountData: null,
      countData: null,
      place: {
        kindergarden: 4,
        hospital: 1,
        elementarySchool: 1,
        middleSchool: 2,
        highSchool: 0,
        mart: 1,
        subway: 1,
        academy: 8,
        caulturalFacility: 1,
      },
      score: {
        apartment: 3,
        education: 4,
        traffic: 5,
        facility: 4,
      },
    };
  },
  methods: {
    initRoadView() {
      this.roadview = new kakao.maps.Roadview(document.getElementById("roadview")); // 
      this.roadviewClient = new kakao.maps.RoadviewClient(); //  ID   helper
    },
    setData() {
      const resolve = (res) => {
        console.log("    ");
        this.data = res.data;
      };
      const reject = (err) => {
        console.log("    !", err);
      };
      getAptDetail(this.target.aptCode, resolve, reject);
    },
    //    
    getDealCountData() {
      // {, { ...[{ ,  }]}}
      let startYear = Number(this.data.buildYear);
      startYear = startYear < 2015 ? 2015 : startYear;

      let data = {};
      for (let item of this.data.list) {
        if (!data[item.area]) {
          data[item.area] = [];
          for (let year = startYear; year <= 2022; year++) {
            data[item.area][year - startYear] = { label: `${year}`, value: 0 };
          }
        }
        data[item.area][item.dealYear - startYear].value++;
      }
      return data;
    },
    //    
    getDealAmountData() {
      // {, { ...[{ ,  }]}}
      let startYear = Number(this.data.buildYear);
      startYear = startYear < 2015 ? 2015 : startYear;

      let data = {};
      for (let item of this.data.list) {
        if (!data[item.area]) {
          data[item.area] = [];
          for (let year = startYear; year <= 2022; year++) {
            data[item.area][year - startYear] = { label: `${year}`, value: 0, count: 0 };
          }
        }
        data[item.area][item.dealYear - startYear].count++;
        data[item.area][item.dealYear - startYear].value += Number(item.dealAmount.split(",").join(""));
      }
      for (let area in data) {
        for (let i = 0; i < data[area].length; i++) {
          if (data[area][i].count) data[area][i].value /= data[area][i].count;
          data[area][i].value = Math.round(data[area][i].value);
        }
      }
      console.log(data);
      return data;
    },
  },
  computed: {
    ...mapState("AptStore", ["target", "aptCode"]),
    getLastDeal() {
      let lastDealAmount = this.data.list[this.data.list.length - 1].dealAmount;
      lastDealAmount = lastDealAmount.split(",").join("") - 0;
      let string = getDealString(lastDealAmount);
      if (string.match(/$/)) return string;
      return string + "";
    },
    getPlaceInfo() {
      const arr = [
        { label: "", value: this.place.kindergarden },
        { label: "", value: this.place.hospital },
        { label: "", value: this.place.elementarySchool },
        { label: "", value: this.place.subway },
        { label: "", value: this.place.middleSchool + this.place.highSchool },
        { label: "", value: this.place.mart },
        { label: "", value: this.place.academy },
        { label: "", value: this.place.caulturalFacility },
      ];
      return arr;
    },
    getScoreInfo() {
      const arr = [
        { label: "", value: this.score.apartment },
        { label: "", value: this.score.education },
        { label: "", value: this.score.traffic },
        { label: "", value: this.score.facility },
      ];
      return arr;
    },
  },
  watch: {
    target() {
      if (this.target) {
        this.initRoadView();
        let position = this.target.latlng;
        //      panoId   .
        this.roadviewClient.getNearestPanoId(position, 100, (panoId) => {
          this.roadview.setPanoId(panoId, position); //panoId    
        });
        this.setData();
      }
    },
    data() {
      this.amountData = this.getDealAmountData();
      this.countData = this.getDealCountData();
    },
  },
  created() {},
  mounted() {
    if (this.target) this.setData();
  },
};
</script>

